/*******Common Css *******/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@200;300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Syncopate:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;500;600;700;800;900&display=swap");

:root {
  --color-primary: #d53f41;
  --color-dark: #626262;
  --color-darker: #464646;
  --color-light: #dbd8d6;
  --color-lighter: #f5f0ec;
  --color-hover: #4400ff;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.7;
  letter-spacing: 1px;
  background-color: var(--color-lighter);
  color: var(--color-darker);
}

.wrapper {
  margin: 0 5vw;
}

section {
  padding: 10vw 0;
}

.section-title {
  font-family: "Syncopate", sans-serif;
  font-size: 1.5rem;
  text-transform: lowercase;
  padding-bottom: 5vw;
  color: var(--color-primary);
}

.heading-text {
  font-size: 3.2rem;
  text-align: center;
  color: var(--color-primary);
}

.min-h-100vh {
  min-width: 100vh;
}

/******* Navbar Design Starts *******/
.navbar {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 1.5rem 0;
  font-family: "Syncopate", sans-serif;
  font-size: 1.5rem;
  text-transform: lowercase;
}
.links-one,
.links-two {
  font-weight: 700;
}

.links-one a,
.links-two a {
  color: var(--color-darker);
  position: relative;
}

.links-one a::after,
.links-two a::after {
  content: "";
  width: 0%;
  height: 0.2rem;
  position: absolute;
  left: 0;
  bottom: -2px;
  /* transform: translate(-50%, -50%); */
  transform: translateX(-100px);
  visibility: hidden;
  background-color: var(--color-primary);
  transition: transform 0.3s cubic-bezier(0.65, -1.13, 0.3, 2.18);
}

.links-one a:hover::after,
.links-two a:hover::after {
  width: 100%;
  transform: translateX(0px);
  visibility: visible;
}

.logo img {
  width: 80%;
}

/******* Hero Deisgn Starts *******/
.hero {
  font-family: "Bai Jamjuree", sans-serif;
  text-transform: uppercase;
  text-align: center;
  font-size: 8vw;
  line-height: 1.45;
  color: var(--color-darker);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.marine,
.technique {
  position: relative;
}

.marine span,
.technique span {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-lighter);
}

.photos {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(6, 1fr);
}

.photo {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.photo.one {
  grid-column: 1;
  grid-row: 2;
}
.photo.two {
  grid-column: 3;
  grid-row: 3;
}
.photo.three {
  grid-column: 2;
  grid-row: 5;
}
.photo.four {
  grid-column: 7;
  grid-row: 4;
}
.photo.five {
  grid-column: 5;
  grid-row: 2;
}

/********* About Design Starts **********/
.about-us {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 5rem;
}

.about-text {
  text-transform: capitalize;
  font-weight: 700;
  font-size: 2rem;
}

.about-left,
.about-right {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}

.about-shutter-left,
.about-shutter-right {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: var(--color-lighter);
}

/******* Featured Design Starts *********/
.features {
  display: grid;
  grid-template-columns: 30% auto;
  align-items: center;
  gap: 10rem;
}

.feature-text {
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: 500;
}

.features img {
  width: 100%;
}

.feature-left,
.feature-right {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}

.feature-shutter-left,
.feature-shutter-right {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: var(--color-lighter);
}

/********* Gallery Design Starts ***********/
.gallery-wrapper {
  background-color: var(--color-primary);
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 10vw;
  padding: 10vw;
}

.gallery-item {
  position: relative;
  width: 50%;
}

.gallery-item-title {
  position: absolute;
  top: 10%;
  left: -50%;
  font-size: 8vw;
  color: var(--color-lighter);
  font-family: "Bai Jamjuree", sans-serif;
  text-transform: uppercase;
  line-height: 1.2;
  mix-blend-mode: color-dodge;
  z-index: 1;
}

.gallery-item-category {
  position: absolute;
  left: 0;
  bottom: -5%;
  font-size: 2rem;
  color: var(--color-lighter);
  text-transform: uppercase;
  letter-spacing: 1rem;
  z-index: 1;
}

.gallery-item-img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}

/********* Service Design Starts *********/
.services {
}

.service-left,
.service-right {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 5rem;
  column-gap: 5rem;
  position: relative;
  padding: 5rem 0;
}

.services img {
  width: 100%;
  height: 100%;
}

.service-shutter-left,
.service-shutter-right {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-lighter);
}

/******** NotFound Design Starts *********/
.not-found {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-content: flex-start;
  column-gap: 5vw;
  row-gap: 2vw;
  padding: 5vw 0;
}

.heading-one,
.heading-two {
  font-family: "Bai Jamjuree", sans-serif;
  font-size: 8vw;
  font-weight: 700;
  line-height: 1;
  text-transform: capitalize;
  color: var(--color-primary);
}

.img-one,
.img-two {
  width: 20vw;
  height: 20vw;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-one img,
.img-two img {
  width: 100%;
  display: block;
}

.img-two {
  justify-self: flex-end;
}

/******* Footer Design Starts *******/
.footer {
  text-align: center;
}

.footer h1 {
  font-family: "Bodoni Moda", sans-serif;
  font-size: 10vw;
  text-transform: lowercase;
  color: var(--color-primary);
}
